.profile-page {
  min-height: calc(100vh - 100px);
  padding: 2rem 1rem;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.profile-container {
  max-width: 800px;
  margin: 0 auto;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.profile-header h1 {
  font-size: 2.5rem;
  color: #333;
  margin: 0;
  font-weight: 700;
}

.edit-btn {
  padding: 0.75rem 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.edit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.profile-card {
  background: white;
  border-radius: 24px;
  padding: 3rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.profile-avatar-section {
  text-align: center;
  margin-bottom: 3rem;
  padding-bottom: 2rem;
  border-bottom: 2px solid #f0f0f0;
}

.profile-avatar {
  font-size: 6rem;
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
  margin: 0 auto 1.5rem;
}

.profile-avatar-section h2 {
  font-size: 2rem;
  color: #333;
  margin: 0 0 0.5rem 0;
  font-weight: 600;
}

.profile-location {
  color: #666;
  font-size: 1rem;
  margin: 0 0 1rem 0;
}

.profile-bio {
  color: #555;
  line-height: 1.6;
  font-size: 1.1rem;
  margin: 0;
  max-width: 600px;
  margin: 0 auto;
}

.profile-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 500px;
  margin: 0 auto;
}

.profile-form input,
.profile-form textarea {
  padding: 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  font-size: 1rem;
  font-family: inherit;
  transition: border-color 0.3s ease;
}

.profile-form input:focus,
.profile-form textarea:focus {
  outline: none;
  border-color: #667eea;
}

.profile-form textarea {
  min-height: 100px;
  resize: vertical;
}

.save-btn {
  padding: 0.75rem 2rem;
  background: linear-gradient(135deg, #51cf66 0%, #40c057 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(81, 207, 102, 0.3);
}

.profile-section {
  margin-bottom: 2.5rem;
}

.profile-section:last-child {
  margin-bottom: 0;
}

.profile-section h3 {
  font-size: 1.25rem;
  color: #333;
  margin: 0 0 1rem 0;
  font-weight: 600;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.tag {
  padding: 0.6rem 1.2rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  display: inline-block;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.tag.value-tag {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}

.editable-tags {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.selected-tags,
.available-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.tag.selected {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tag.selected:hover {
  background: linear-gradient(135deg, #5568d3 0%, #653a8f 100%);
  transform: scale(1.05);
}

.tag.available {
  background: #f0f0f0;
  color: #666;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px dashed #ccc;
}

.tag.available:hover {
  background: #e0e0e0;
  border-color: #667eea;
  color: #667eea;
}
