.groups-page {
  min-height: calc(100vh - 100px);
  padding: 2rem 1rem;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.groups-container {
  max-width: 1200px;
  margin: 0 auto;
}

.page-header {
  text-align: center;
  margin-bottom: 3rem;
}

.page-header h1 {
  font-size: 2.5rem;
  color: #333;
  margin: 0 0 0.5rem 0;
  font-weight: 700;
}

.page-header p {
  color: #666;
  font-size: 1rem;
  margin: 0 0 1.5rem 0;
}

.create-group-btn {
  padding: 0.75rem 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.create-group-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.create-group-form {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 3rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.create-group-form form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.create-group-form input,
.create-group-form textarea {
  padding: 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  font-size: 1rem;
  font-family: inherit;
  transition: border-color 0.3s ease;
}

.create-group-form input:focus,
.create-group-form textarea:focus {
  outline: none;
  border-color: #667eea;
}

.create-group-form textarea {
  min-height: 100px;
  resize: vertical;
}

.create-group-form button {
  padding: 0.75rem 2rem;
  background: linear-gradient(135deg, #51cf66 0%, #40c057 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.create-group-form button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(81, 207, 102, 0.3);
}

.groups-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.group-card {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.group-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.group-avatar {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.group-name {
  font-size: 1.5rem;
  color: #333;
  margin: 0 0 0.75rem 0;
  font-weight: 600;
}

.group-description {
  color: #666;
  line-height: 1.6;
  margin: 0 0 1rem 0;
}

.group-stats {
  margin-bottom: 1rem;
}

.members-count {
  color: #888;
  font-size: 0.9rem;
}

.group-interests {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.interest-badge {
  padding: 0.4rem 0.9rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.join-btn {
  width: 100%;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #51cf66 0%, #40c057 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(81, 207, 102, 0.3);
}

.join-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(81, 207, 102, 0.4);
}
