.swipe-page {
  min-height: calc(100vh - 100px);
  padding: 2rem 1rem;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.swipe-container {
  max-width: 600px;
  margin: 0 auto;
}

.swipe-header {
  text-align: center;
  margin-bottom: 2rem;
}

.swipe-header h1 {
  font-size: 2.5rem;
  color: #333;
  margin: 0 0 0.5rem 0;
  font-weight: 700;
}

.swipe-header p {
  color: #666;
  font-size: 1rem;
  margin: 0;
}

.swipe-progress {
  text-align: center;
  margin-top: 2rem;
  color: #888;
  font-size: 0.9rem;
}

.empty-state {
  text-align: center;
  padding: 4rem 2rem;
  background: white;
  border-radius: 24px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  margin: 4rem auto;
}

.empty-icon {
  font-size: 5rem;
  margin-bottom: 1rem;
}

.empty-state h2 {
  color: #333;
  margin: 0 0 1rem 0;
  font-size: 1.75rem;
}

.empty-state p {
  color: #666;
  line-height: 1.6;
  margin: 0;
}
